// TODO: add fullscreen support
export default class Canvas {
  constructor(options) {
    this.options = options;

    console.log(options);

    // canvas element in DOM
    this.element;
    this.elementId = "scene";

    this.fullScreen = this.options.fullScreen || false;
    this.width = this.fullScreen ? window.innerWidth : this.options.width;
    this.height = this.fullScreen ? window.innerHeight : this.options.height;

    this.pixelRatio = process.env.NODE_ENV === "test" ? 1 : window.devicePixelRatio;

    this.clearBeforeRender = this.options.clearBeforeRender;
    this.preserveDrawingBuffer = this.options.preserveDrawingBuffer || false;
    this.transparent = this.options.transparent || false;

    this.init();
  }

  init() {
    if (process.env.NODE_ENV === "test") { return; }

    this.createCanvas();

    // adds resizes if we're in fullscreen mode
    if (this.fullScreen) {
      window.addEventListener("resize", () => {
        this.resize();
      });
    }
  }

  /**
   * Creates an canvas element to the page
   */
  createCanvas() {
    // create a new DOM element
    this.element = document.createElement("canvas");
    this.element.setAttribute("id", this.elementId);

    // canvas attributes
    this.element.width = this.width * this.pixelRatio;
    this.element.height = this.height * this.pixelRatio;

    // add to document
    document.body.appendChild(this.element);
    // styles
    this.addStyles();
  }

  /**
   * Adds an stylesheet into the head of HTML
   */
  addStyles() {
    const styleElement = document.createElement("style");

    // Append <style> element to <head>
    document.head.appendChild(styleElement);

    const styleSheet = styleElement.sheet;

    const rules = `
    #${this.elementId} {
      width: ${this.width}px;
      height: ${this.height}px;
      margin: 0;
      position: absolute;
      top: 0;
      left: 0;
      overflow: hidden;
    }
    `;

    styleSheet.insertRule(rules, 0);
  }

  // only in fullscreen
  resize() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.element.width = this.width * this.pixelRatio;
    this.element.height = this.height * this.pixelRatio;
    this.element.style.width = `${this.width}px`;
    this.element.style.height = `${this.height}px`;
  }

  destroy() {
    this.options = null;
    this.element = null;
  }
}