import { createShaderProgram } from "./webglutils/program";
import { getVertexShader, getFragmentShader } from "./shaders/renderer2D";

/**
 * Renderer object is an interface between WebGL and the graphics program.
 * It is in charge of shader programming, and drawing colors and geometry on screen.
 */

export default class Renderer2D {
  constructor(canvas) {
    // scene object
    this.scene;

    // canvas object
    this.canvas = canvas;

    // reference to the WebGL
    this.gl;

    // dimentions
    this.resolution = [this.canvas.width, this.canvas.height];

    // basic program
    this.programs = {};

    // measurements
    this.frameRate = 0;
    this.frameCount = 0;
    this.previousTime = 0;
    this.drawCalls = 0;

    // switches
    this.stopRenderLoopGiven = false;

    this.init();
  }

  /**
   * Is called in construction
   */
  init() {
    if (process.env.NODE_ENV === "test") { return; }

    const gl = this.setupGL();

    // a basic shader program that works good with most stuff
    // coloring is per element and not per vertex
    const basicVert = getVertexShader("basic");
    const basicFrag = getFragmentShader("basic");
    this.programs.basic = createShaderProgram(gl, basicVert, basicFrag);

    // a separate shader program that calculates parents rotations in GPU
    const childVert = getVertexShader("child");
    const childFrag = getFragmentShader("child");
    this.programs.child = createShaderProgram(gl, childVert, childFrag);

    // shader program for points data
    const pointsVert = getVertexShader("points");
    const pointsFrag = getFragmentShader("points");
    this.programs.points = createShaderProgram(gl, pointsVert, pointsFrag);

    // shader program for points data
    const linesVert = getVertexShader("lines");
    const linesFrag = getFragmentShader("lines");
    this.programs.lines = createShaderProgram(gl, linesVert, linesFrag);
  }

  /**
   * sets this.gl to be a WebGL2RenderingContext interface
   * @returns {WebGL2RenderingContext}
   */
  setupGL() {
    const gl = this.canvas.element.getContext("webgl2", { preserveDrawingBuffer: this.canvas.preserveDrawingBuffer });
    this.gl = gl;
    return gl;
  }

  /**
   * Sets buffers and attributes for each draw call
   * @param {WebGL2RenderingContext} gl 
   * @param {object} info 
   */
  setBuffersAndAttributes(gl, info) {
    const program = info.program;
    const positionBuffer = info.position;
    const indicesBuffer = info.index;

    // position attribute in to the shader program
    const positionLocation = gl.getAttribLocation(program, "a_position");
    gl.enableVertexAttribArray(positionLocation);
    // always bind before setting attribute
    gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
    gl.vertexAttribPointer(positionLocation, 2, gl.FLOAT, false, 0, 0);

    if (info.drawElements) {
      // bind element array buffer
      gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indicesBuffer);
    }
  }

  /**
   * Sets uniforms for each draw call
   * @param {WebGL2RenderingContext} gl 
   * @param {WebGLProgram} program
   * @param {AbstractShape} shape
   */
  setUniforms(gl, program, shape) {
    const position = gl.getUniformLocation(program, "u_position");
    const rotation = gl.getUniformLocation(program, "u_rotation");
    const color = gl.getUniformLocation(program, "u_color");
    const time = gl.getUniformLocation(program, "u_time");
    const resolution = gl.getUniformLocation(program, "u_resolution");
    
    // if shape has parent take parents rotation in count
    if (shape.hasParent) {
      const parentPosition = gl.getUniformLocation(program, "u_parent_position");
      const parentRotation = gl.getUniformLocation(program, "u_parent_rotation");
      gl.uniform2fv(parentPosition, shape.parent.position.getAsArray());
      gl.uniform1f(parentRotation, shape.parent.rotation);
    }

    gl.uniform1f(time, this.previousTime / 1000);
    gl.uniform2fv(resolution, this.resolution);
    gl.uniform2fv(position, shape.position.getAsArray());
    gl.uniform1f(rotation, shape.rotation);
    gl.uniform4fv(color, shape.color.getAsArray());
  }

  /**
   * renders teh pending image once
   * has to be given to the rendering loop separately
   */
  render() {
    const gl = this.gl;

    gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);

    // clears scene with background color
    if (this.canvas.clearBeforeRender) {

      const alpha = this.canvas.transparent ? 0.0 : 1.0;

      gl.clearColor(0.95, 0.95, 0.95, alpha);
      gl.clear(gl.COLOR_BUFFER_BIT);
    }

    const drawCalls = this.scene.shapes.length;
    // draw things only if there is something to draw
    if (drawCalls > 0) {

      // TODO: draw calls ready for different programs

      // draw calls for each shape
      this.scene.shapes.forEach(shape => {
        const programInfo = shape.programInfo;
        const program = programInfo.program;

        // specify, witch program is in use
        gl.useProgram(program);

        // then setup the correct variables for shader programs
        this.setBuffersAndAttributes(gl, programInfo);        
        this.setUniforms(gl, program, shape);

        // check how the elements are drawn
        if (programInfo.drawElements) {

          // draws indexed positions
          gl.drawElements(programInfo.drawMode, shape.amountOfVertices, gl.UNSIGNED_SHORT, 0);

        } else {

          // draws positions in order, requires an oredered array
          gl.drawArrays(programInfo.drawMode, 0, shape.vertexData.positions.length / 2);

        }
      });

      // only then add to the frame count
      this.frameCount += 1;
    }

    // keep track of the draw calls
    this.drawCalls = drawCalls;
  }

  /**
   * Render loop initialzer
   * @param {(delta: number) => void} func things that happen between frames
   */
  runRenderLoop(func) {
    // Resets previous commands if renderloop was stopped
    this.stopRenderLoopGiven = false;

    // starts looping
    window.requestAnimationFrame((currentTime) => {
      const delta = (currentTime - this.previousTime) / 1000;
      this.updateFrameRate(delta);

      this.previousTime = currentTime;
      this.resolution[0] = this.canvas.width;
      this.resolution[1] = this.canvas.height;

      // the given function inside runRenderLoop call
      func(delta);

      if (!this.stopRenderLoopGiven) {
        this.runRenderLoop(func);
      }
    });
  }

  /**
   * Draws points directly to the canvas from positition and color data.
   * @param {Array} positionsArray 
   * @param {Array} colorsArray 
   * @param {number} size 
   */
  drawPoints(positionsArray, colorsArray, size) {
    if (positionsArray.length * 2 !== colorsArray.length) { console.error(`Points array lengths don't match pos: ${positionsArray.length} and color: ${colorsArray.length}`); }

    const gl = this.gl;
    const program = this.programs.points;

    const positions = positionsArray;
    const colors = colorsArray;

    gl.useProgram(program);

    // positions buffer
    const positionsBuffer = gl.createBuffer();
    const positionLocation = gl.getAttribLocation(program, "a_position");
    gl.enableVertexAttribArray(positionLocation);
    gl.bindBuffer(gl.ARRAY_BUFFER, positionsBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(positions), gl.STATIC_DRAW);
    gl.vertexAttribPointer(positionLocation, 2, gl.FLOAT, false, 0, 0);

    // colors buffer
    const colorBuffer = gl.createBuffer();
    const colorLocation = gl.getAttribLocation(program, "a_color");
    gl.enableVertexAttribArray(colorLocation);
    gl.bindBuffer(gl.ARRAY_BUFFER, colorBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(colors), gl.STATIC_DRAW);
    gl.vertexAttribPointer(colorLocation, 4, gl.FLOAT, false, 0, 0);

    const time = gl.getUniformLocation(program, "u_time");
    const pointSize = gl.getUniformLocation(program, "u_size");
    const resolution = gl.getUniformLocation(program, "u_resolution");

    gl.uniform1f(time, this.previousTime / 1000);
    gl.uniform1f(pointSize, size * this.canvas.pixelRatio);
    gl.uniform2fv(resolution, this.resolution);

    gl.drawArrays(gl.POINTS, 0, positions.length / 2);
  }

  /**
   * Draw lines directly to the canvas from positions data.
   * Lines can be only 1px thick rendered with this function.
   * @param {Array} positionsArray 
   * @param {number} color 
   */
  drawLines(positionsArray, lineColor) {
    const gl = this.gl;
    const program = this.programs.lines;

    const positions = positionsArray;

    gl.useProgram(program);

    // positions buffer
    const positionsBuffer = gl.createBuffer();
    const positionLocation = gl.getAttribLocation(program, "a_position");
    gl.enableVertexAttribArray(positionLocation);
    gl.bindBuffer(gl.ARRAY_BUFFER, positionsBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(positions), gl.STATIC_DRAW);
    gl.vertexAttribPointer(positionLocation, 2, gl.FLOAT, false, 0, 0);

    const time = gl.getUniformLocation(program, "u_time");
    const color = gl.getUniformLocation(program, "u_color");
    const resolution = gl.getUniformLocation(program, "u_resolution");

    gl.uniform1f(time, this.previousTime / 1000);
    gl.uniform4fv(color, lineColor.getAsArray());
    gl.uniform2fv(resolution, this.resolution);

    gl.drawArrays(gl.LINES, 0, positions.length / 2);
  }

  /**
   * Updates frame rate
   * @param {number} delta The time it takes to draw a single frame 
   */
  updateFrameRate(delta) {
    this.frameRate = Math.floor(1 / delta);
  }

  /**
   * Stops the render loop
   */
  stop() {
    this.stopRenderLoopGiven = true;
  }

  /**
   * Destroys the renderer and ends the render loop
   */
  destroy() {
    this.stop();
    this.programs = null;
    this.resolution = null;
  }
}