import AbstractShape from "./abstractshape";
import VertexData from "./geometry/vertexdata";
import Color from "../utils/color";
import Vec2 from "../utils/math/vector2";

export default class Circle extends AbstractShape {
  constructor(scene, options) {
    super(scene);

    // options object
    this.options = options || {};

    // different options 
    this.radius = this.options.radius || 50;
    this.arc = this.options.arc || 1.0;
    this.tesselation = this.options.tesselation || 64;
    this.color = this.options.color || new Color(0.0, 0.5, 0.5);

    // variables you need to change
    this.position = new Vec2(0, 0);
    this.rotation = 0;

    this.init();
  }

  init() {
    const data = this.buildGeometry();
    this.buildProgramInfo(data);
  }

  buildGeometry() {
    const arc = this.arc;
    const tesselation = this.tesselation;
    const radius = this.radius;

    const positions = [];
    const indices = [];

    // disc center first
    positions.push(0, 0);
    
    const theta = Math.PI * 2 * arc;
    const step = theta / tesselation;
    
    // circle closes only if we go half step over in the for loop
    for (let a = 0; a < theta + step/2; a += step) {
      const x = radius * Math.cos(a);
      const y = radius * Math.sin(a);
      positions.push(x, y);
    }

    //indices
    var vertexNb = positions.length / 2;
    for (var i = 1; i < vertexNb - 1; i++) {
      indices.push(i + 1, 0, i);
    }

    const vertexData = new VertexData(positions, indices);
    this.vertexData = vertexData;

    // save the number of vertices shape would have on screen
    this.amountOfVertices = indices.length;

    return vertexData;
  }

  /**
   * Builds program-info -object witch passes information about renderig to the renderer.
   * @param {VertexData} data 
   */
  buildProgramInfo(data) {
    if (process.env.NODE_ENV === "test") { this.programInfo = {}; return; }

    const gl = this.scene.renderer.gl;
    const program = this.scene.renderer.programs.basic;

    const positionBuffer = gl.createBuffer();
    gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer);
    gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(data.positions), gl.STATIC_DRAW);

    const indicesBuffer = gl.createBuffer();
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indicesBuffer);
    gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, new Uint16Array(data.indices), gl.STATIC_DRAW);

    const info = {
      position: positionBuffer,
      index: indicesBuffer, 
      drawMode: gl.TRIANGLES,
      program: program,
      drawElements: true
    };

    this.programInfo = info;
  }
}