import Canvas from "./canvas";
import Scene from "./scene";
import Renderer2D from "./renderer2D";
import Rectangle from "./shapes/rectangle";
import Circle from "./shapes/circle";
import CustomShape from "./shapes/custom";
import VertexData from "./shapes/geometry/vertexdata";

import Vec2 from "./utils/math/vector2";
import Color from "./utils/color";
import Matrix3 from "./utils/math/matrix3";

const basicOptions = process.env.NODE_ENV === "test" ? {
  width: 1000,
  height: 1000,
  fullScreen: false,
  clearBeforeRender: false,
  preserveDrawingBuffer: false,
  transparent: false
} : {
  width: window.innerWidth,
  height: window.innerHeight,
  fullScreen: false,  
  clearBeforeRender: false,
  preserveDrawingBuffer: false,
  transparent: false
};

/**
 * Copyright 2019 Oliver Mulari
 * 
 * A simple 2D graphics engine for WebGL2
 */
export default class GRAPHICS {
  /**
   * @param {object} options {width: number, height: number, fullScreen: boolean}
   */
  constructor(options) {
    // makes sure that basic options are set
    this.options = Object.assign(basicOptions, options || {});

    this.canvas;
    this.renderer;
    this.scene;

    this.init();
  }

  init() {
    const canvas = new Canvas(this.options);
    const renderer = new Renderer2D(canvas);
    const scene = new Scene(canvas, renderer);
    renderer.scene = scene;

    this.canvas = canvas;
    this.scene = scene;
    this.renderer = renderer;
  }

  destroy() {
    this.canvas.destroy();
    this.renderer.destroy();
    this.scene.destroy();
    this.canvas = null;
    this.renderer = null;
    this.scene = null;
  }
}

// Some routing

GRAPHICS.Scene = Scene;
GRAPHICS.Renderer2D = Renderer2D;

// Shapes

GRAPHICS.Circle = Circle;
GRAPHICS.Rectangle = Rectangle;
GRAPHICS.CustomShape = CustomShape;

// Geometry

GRAPHICS.VertexData = VertexData;

// Utils

GRAPHICS.Vec2 = Vec2;
GRAPHICS.Matrix3 = Matrix3;
GRAPHICS.Color = Color;