import * as PIXI from 'pixi.js';

import { Vec2 } from "./Vector";
import { generateMaze } from "./mazegenerator";
import { mapDistances } from "./pathfinding";
import { presets } from "./presets";

export class Grid {
  constructor(app, options) {
    this.app = app;
    this.options = Object.assign(presets(options.preset || "Basic"), options);

    // mobile test
    this.isMobile = PIXI.utils.isMobile.any;

    // for maze
    this.blockSize = this.options.blockSize;
    this.cols = Math.ceil(this.app.screen.width / this.blockSize);
    this.rows = Math.ceil(this.app.screen.height / this.blockSize);

    // for pathfindig
    this.tilesInBlock = this.options.tilesInBlock;
    this.tileSize = this.blockSize / this.tilesInBlock;
    this.tileGraphics = new PIXI.Graphics();
    this.tiles = [];

    //source
    this.tileRows = this.rows * this.tilesInBlock;
    this.tileCols = this.cols * this.tilesInBlock;
    this.source = Math.floor(this.tileCols / 2) + this.tileCols * Math.floor(this.tileRows / 2)

    // arrays of elements
    this.points = [];
    this.horizontalWalls = [];
    this.verticalWalls = [];

    // colors
    this.wallColor = this.options.wallColor;
    this.colorAmp = this.options.colorAmp;

    this.frameCount = 0;

    // start function
    this.init();
  }

  init() {
    this.createPoints();
    this.createWalls();
    generateMaze(this);
    // also draws them
    this.createTiles();

    // walls on top of tiles
    if (this.options.wallColor) {
      this.showWalls();
    }

    // inits event listener
    this.updatePointerPos();
  }

  createPoints() {    
    const offX = (this.app.screen.width - (this.cols * this.blockSize)) / 2
    const offY = (this.app.screen.height - (this.rows * this.blockSize)) / 2

    for (let y = 0; y <= this.rows; y++) {
      for (let x = 0; x <= this.cols; x++) {
        const point = new Vec2((x * this.blockSize) + offX, (y * this.blockSize) + offY);
        this.points.push(point);
      }
    }
  }

  createWalls() {
    // vertical
    for (let y = 0; y < this.rows; y++) {
      const vRow = [];
      for (let x = 0; x <= this.cols; x++) {
        vRow.push(true);
      }
      this.verticalWalls.push(vRow);
    }

    // horizontal
    for (let y = 0; y <= this.rows; y++) {
      const hRow = [];
      for (let x = 0; x < this.cols; x++) {
        hRow.push(true);
      }
      this.horizontalWalls.push(hRow);
    }
  }

  breakWall(row, column, dir) {
    switch (dir) {
      case "Down":
        if (row == this.rows - 1) {
          console.log("Can't break bottom walls")
        } else if (!this.horizontalWalls[row + 1][column]) {
          console.log("Can't break walls that are already broken")
        } else {
          this.horizontalWalls[row + 1][column] = false
        }
        break;

      case "Up":
        if (row == 0) {
          console.log("Can't break top walls")
        } else if (!this.horizontalWalls[row][column]) {
          console.log("Can't break walls that are already broken")
        } else {
          this.horizontalWalls[row][column] = false
        }
        break;

      case "Left":
        if (column == 0) {
          console.log("Can't break leftmost walls")
        } else if (!this.verticalWalls[row][column]) {
          console.log("Can't break walls that are already broken")
        } else {
          this.verticalWalls[row][column] = false
        }
        break;

      case "Right":
        if (column == this.cols) {
          console.log("Can't break rightmost walls")
        } else if (!this.verticalWalls[row][column + 1]) {
          console.log("Can't break walls that are already broken")
        } else {
          this.verticalWalls[row][column + 1] = false
        }
        break;
    }
  }

  showWalls() {
    const graphics = new PIXI.Graphics();
    this.app.stage.addChild(graphics);

    for (let y = 0; y <= this.rows; y++) {
      for (let x = 0; x <= this.cols; x++) {

        // get current point
        const point = this.points[x + y * (this.cols+1)]

        // test horizontal walls
        if (this.horizontalWalls[y][x]) {
          graphics.lineStyle(1, this.wallColor)
              .moveTo(point.x, point.y)
              .lineTo(point.x + this.blockSize, point.y);
          graphics.endFill();
        }

        // test verical walls
        if (this.verticalWalls[y] && this.verticalWalls[y][x]) {
          graphics.lineStyle(1, this.wallColor)
              .moveTo(point.x, point.y)
              .lineTo(point.x, point.y + this.blockSize);
          graphics.endFill();
        }

      }
    }
  }

  createTiles() {
    const tileRows = this.rows * this.tilesInBlock;
    const tileCols = this.cols * this.tilesInBlock;

    const offX = (this.app.screen.width - (this.cols * this.blockSize)) / 2
    const offY = (this.app.screen.height - (this.rows * this.blockSize)) / 2

    for (let y = 0; y < tileRows; y++) {
      for (let x = 0; x < tileCols; x++) {
        const tile = new Vec2((x * this.tileSize) + offX, (y * this.tileSize) + offY);
        this.tiles.push(tile);
      }
    }

    this.app.stage.addChild(this.tileGraphics);
    this.updateTiles();
  }

  updateTiles() {
    const dist = mapDistances(this, this.source);
    const func = this.fillFunc();

    this.tileGraphics.clear();
    this.tiles.forEach((tile, i) => {
      const c = dist[i]*this.colorAmp;
      this.tileGraphics.beginFill(func(c));
      this.tileGraphics.drawRect(tile.x, tile.y, this.tileSize, this.tileSize);
    });
  }

  updatePointerPos() {
    const offX = (this.app.screen.width - (this.cols * this.blockSize)) / 2
    const offY = (this.app.screen.height - (this.rows * this.blockSize)) / 2
    document.onpointermove = (e) => {
      const x = e.clientX - offX;
      const y = e.clientY - offY;
      const tileX = (x-x%this.tileSize)/this.tileSize;
      const tileY = (y-y%this.tileSize)/this.tileSize;
      if (x >= 0 && y >= 0 && tileX < this.cols*this.tilesInBlock && tileY < this.rows*this.tilesInBlock) {
        this.source = Math.floor(tileX+this.cols*this.tilesInBlock*tileY);
      }
    }
  }

  fillFunc() {
    const g = this.options.grey;
    const ig = this.options.greyInvert;
    if (g) {
      return (c) => {
        const v = Math.max(g, 255-c);
        return this.rgb(v, v, v);
      }
    } else if (ig) {
      return (c) => {
        const a = Math.min(255, c+ig);
        return this.rgb(a, a, a);
      }
    } else {
      return (c) => this.rgb(Math.min(255, c), 255, 255);
    }
  }

  rgb(r, g, b) {
    return ((r << 16) + (g << 8) + b);
  }

  update(delta) {
    if (this.frameCount % 100) {
      this.updateTiles();
    }
    this.frameCount++;
  }
}
